<template>
	<scroll-view ref="scrollviewRef"
		:refresher-triggered="triggered"
		@refresherpulling="onPulling"
		@refresherrefresh="onRefresh" 
		@refresherrestore="onRestore"
		scroll-y="true" 
		refresher-enabled="true" 
		v-if="isShow">
		<Button @click="callRefresh">点我刷新</Button>
		<!-- 推荐item -->
		<view v-for="item in dataSource" :key="item.id" class="subscribeItem">
			<!-- 推荐item标题 -->
			<text class="subscribeItem_title">{{item.title}}</text>
			<!-- 推荐item额外信息 -->
			<view class="subscribeItem_info">
				<uni-tag :text="item.tag" :type="item.tagType" v-if="item.tag" class="subscribeItem_tag">
				</uni-tag>
				<text>{{item.source}}</text>
				<text v-if="item.readNum">{{item.readNum}}阅读</text>
				<text>{{item.time}}</text>
			</view>
		</view>
	</scroll-view>
</template>

<script>
	export default {
		name: "TabsContent",
		props: {
			isShow: {
				type: Boolean,
				default: true
			},
			dataSource: {
				type: Array,
				default: []
			},
			triggered:{
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				isRefreshing : false,
			};
		},
		methods: {
			callRefresh(e){
				console.log(e)
				console.log(this.$refs.scrollviewRef.$listeners)
				this.$refs.scrollviewRef.$listeners.refresherpulling()
				this.$refs.scrollviewRef.$listeners.refresherrefresh()
			},
			// 下拉钩子
			onPulling(e) {
				// console.log("onpulling", e);
				this.$emit("pull",e)
			},
			// 刷新中的钩子
			onRefresh(e) {
				if(this.isRefreshing){
					return 
				}
				this.isRefreshing = true
				// console.log("onRefresh",e);
				this.$emit('refresh',e)
			},
			onRestore(){
				this.isRefreshing = false
			}
		}
	}
</script>

<style lang="scss">
	.refreshNotice{
		width: 100%;
		text-align: center;
	}
	.subscribeItem {
		border-bottom: 1px solid #ccc;
		box-sizing: border-box;
		padding: 30rpx 0;

		.subscribeItem_title {
			font-size: 30rpx;
		}

		.subscribeItem_info {
			font-size: 27rpx;
			margin-top: 30rpx;
			color: #bbb;

			.subscribeItem_tag {
				margin: 0 10rpx 0 0;
			}

			text {
				margin: 0 10rpx;
			}
		}

	}
</style>
